// import logo from './logo.svg';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import './App.css';
import { Login } from "./Models/Login/Login";
import { Header } from "./Models/Header/header";
import { Register } from "./Models/Register/Register";
import { User } from "./Models/Users/User";
import { UserDashboard } from "./Models/UserDashboard/UserDashboard";
import { Products } from "./Models/Products/Product";
import { GenerateProduct } from "./Models/CreateProduct/ProductCreate";
import { AdminDashboard } from "./Models/AdminDashboard/AdminDashboard";
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import axios from 'axios';
import { Card } from "./Models/Cards/Card";

//            ************************************************************
function App() {

  let dispatch = useDispatch();

  useEffect(function(){

    axios.post(
      '/check-session', 
      {token:localStorage.getItem('userToken')}
      ).then(
        function(resp){

          if(resp.data){
            dispatch({
              type:"LOGIN_HOGYA",
              payload:resp.data
            });

            // console.log(resp.data)
          } 
          // console.log(resp);
        }
        
      )
      // console.log(token);


  }, []);
  return (
    <div>
      <BrowserRouter>
        <Header></Header>
        <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/register" element={<Register />} />
        <Route path="/register/:name" element={<Register />} />
        <Route path="/users" element={<User />} />
        <Route path="/card" element={<Card />} />
        <Route path="/userDashboard" element={<UserDashboard />} />
        <Route path="/products" element={<Products />} />
        <Route path="/productscreate" element={<GenerateProduct />} />
        <Route path="/adminDashboard" element={<AdminDashboard />} />
        <Route path="/productscreate/:name" element={<GenerateProduct />} />


        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
