import axios from 'axios';
import React, { useState ,useEffect } from 'react'
import Table from 'react-bootstrap/Table';
import { Link } from 'react-router-dom';
import swal from 'sweetalert';
import './User.css'


export function User(props) {
  // let dispatch = useDispatch();

  useEffect(()=>{
    axios.get('/user').then(
      function(res){
      setUsers(res.data) 
      // console.log(res.data);
    })
  },[])

  let [users, setUsers] = useState([])


  return (
    <div>
      <Table striped bordered hover size="sm" variant="dark" border='1px solid'>
        <thead>
          <tr>
            <td>User Name</td>
            <td>Email</td>
            <td>Password</td>
            <td>Mobile No.</td>
            <td>Picture</td>
            <td>Delete/Update</td>
          </tr>
        </thead>
        {
          users.map(function(user,index){
            
                return (
                  <>
                    <tbody>
                      <tr>
                        <td>{user.username}</td>
                        <td>{user.email}</td>
                        <td>{user.password}</td>
                        <td>{user.mobile}</td>
                        <td><img id='image_f' src={user.pic} alt="f" /></td>
                        <td>
                          <button id='del_btn' onClick={()=>{
                            axios.delete('/user-delete?anc='+user.id).then(function(resp){
                              setUsers(users.filter(myUser=>myUser.id !== user.id));
                              // console.log(resp.data);
                            }); 
                            // console.log(user.id);
                            swal({
                                  title: "Success",
                                  text: "User Deleted Successfully",
                                  icon: "success",
                                  timer: 2000,
                                  button: false
                                })
                            // dispatch({
                            //     type:'PERSON_DELETED',
                            //     index:index
                            //   })
                          }}>Delete</button>
                          <button><Link to={'/register/'+ user.id}>Update</Link></button>
                          </td>
                        </tr>
                    </tbody>
                  </>
                );
            })
        }
    </Table>
    </div>
  )
}

