import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import './header.css'

//          ******************************************************             //
export const Header = () => {

  let dispatch = useDispatch();
  
  let users = useSelector(function(store){
    return store.userSection.currentUser;
  })

  const [click, setClick] = useState(false);  

  const handleClick = () => setClick(!click);
  const Close = () => setClick(false);

  return (
    <>
    <div className={click ? 'main-container' : ""} 
         onClick={()=>Close()}/>

      <nav className='navbar' onClick={e => e.stopPropagation()}>

        <Link id='logo_link' to='/'>
        <svg id="logo-85"  width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg"><path class="ccustom" fill-rule="evenodd" clip-rule="evenodd" d="M10 0C15.5228 0 20 4.47715 20 10V0H30C35.5228 0 40 4.47715 40 10C40 15.5228 35.5228 20 30 20C35.5228 20 40 24.4772 40 30C40 32.7423 38.8961 35.2268 37.1085 37.0334L37.0711 37.0711L37.0379 37.1041C35.2309 38.8943 32.7446 40 30 40C27.2741 40 24.8029 38.9093 22.999 37.1405C22.9756 37.1175 22.9522 37.0943 22.9289 37.0711C22.907 37.0492 22.8852 37.0272 22.8635 37.0051C21.0924 35.2009 20 32.728 20 30C20 35.5228 15.5228 40 10 40C4.47715 40 0 35.5228 0 30V20H10C4.47715 20 0 15.5228 0 10C0 4.47715 4.47715 0 10 0ZM18 10C18 14.4183 14.4183 18 10 18V2C14.4183 2 18 5.58172 18 10ZM38 30C38 25.5817 34.4183 22 30 22C25.5817 22 22 25.5817 22 30H38ZM2 22V30C2 34.4183 5.58172 38 10 38C14.4183 38 18 34.4183 18 30V22H2ZM22 18V2L30 2C34.4183 2 38 5.58172 38 10C38 14.4183 34.4183 18 30 18H22Z" fill="#17cf97"></path></svg>
        <span id='title'>Home</span>
        </Link>
        {users.id !==undefined &&  <span id='user_box'> <img id='image_u' src={users.pic} alt='' /> <span id='wel'> Welcome,  <b>{users.username}</b></span> </span> }
        <div className={click ? "nav-menu active" : "nav-menu"}>
          <ul id='navLink'>
            <li className='nav-item'>
              {users.id !== undefined && users.username !== 'talha' && <Link to='/userDashboard' activeClassName="active" 
                className="nav-links"
                onClick={click ? handleClick : null}>
                {users.username + "' Dashboard"}
              </Link>}
              {users.id !== undefined && users.username === 'talha' && <Link to='/adminDashboard' activeClassName="active" 
                className="nav-links"
                onClick={click ? handleClick : null}>
                {users.username + "' Dashboard"}
              </Link>}
              {users.id !== undefined && users.username === 'talha' && <Link to='/card' activeClassName="active"
                className="nav-links"
                onClick={click ? handleClick : null}>
                Users
              </Link>}
              {users.id !== undefined && users.username === 'talha' && <Link to='/products' activeClassName="active"
                className="nav-links"
                onClick={click ? handleClick : null}>
                Products
              </Link>}
              {users.id !== undefined && <Link to='/productscreate' activeClassName="active"
                className="nav-links"
                onClick={click ? handleClick : null}>
                CreateProduct
              </Link>}
              {users.id === undefined && <Link to='/login' activeClassName="active"
                className="nav-links"
                onClick={click ? handleClick : null}>
                Login
              </Link>}
              {users.id === undefined && <Link to='/register' activeClassName="active"
                className="nav-links"
                onClick={click ? handleClick : null}>
                Register
              </Link>}
              {users.id !== undefined &&<Link to='/login' activeClassName="active"
                className="nav-links"
                onClick={()=>{
                  dispatch({
                    type:"LOGOUT_HOGYA"
                  })
                }}>
                Logout
              </Link>}
            </li>
          </ul>
        </div>
        <div className="nav-icon" onClick={handleClick}>
            <i className={click ? "fa fa-times" : "fa fa-bars"}></i>
          </div>
        
      </nav>
    </>
  )
}