
// import { createStore } from 'redux'
import { combineReducers, createStore } from 'redux'

let initialArr = {
  users: [],
  currentUser:{}
};

function userSection(oldData = initialArr,newData){
  // if(newData.type === 'PERSON_ADD'){
  //   oldData.users.push(newData.newName)
  // }

  // else if(newData.type === 'PERSON_DELETED'){
  //   oldData.users.splice(newData.index,1)
  // }

  // else if(newData.type === 'PERSON_EDITTED'){
  //   let someIndex = oldData.users.findIndex
  //   (user=>user.id === newData.payload.id);
  //       oldData[someIndex]  = newData.payload;
  // }

  if(newData.type === "LOGIN_HOGYA"){

    oldData.currentUser = newData.payload;

  }else if(newData.type === "LOGOUT_HOGYA"){
    
    oldData.currentUser = {};
    localStorage.removeItem('userToken');
    
  }
  


  return {...oldData, users:[...oldData.users]}
}

let initial = {
  products:[]
}

function productSection(oldData = initial, newData){

 if(newData.type === "ADD_PRODUCT"){
  oldData.products.push(newData.payload)
 }

 return {...oldData};
}

let data = combineReducers({userSection, productSection});

// export let meraStore = createStore(userSection);
export let meraStore = createStore(data);