import React, { useEffect, useState } from 'react'
// import bgImg from '../../Images/img1.jpg';
import productimage from '../../Images/product.jpg';
import { useForm } from 'react-hook-form';
import './ProductCreate.css'
import {v4} from 'uuid';
import axios from 'axios';
// import { Link } from 'react-router-dom';
import { useNavigate, useParams } from "react-router-dom"
import swal from 'sweetalert';
import { useSelector } from 'react-redux';

export function GenerateProduct() {
  
  const { register, handleSubmit, formState: { errors },reset} = useForm()
  
  const navigate = useNavigate();
  let params = useParams();
  const [products, setProducts] = useState(null);

  let user = useSelector(function(store){
    return store.userSection.currentUser;
  })

  useEffect(function(){
    console.log(params);
    axios.get('/product-lao?id=' + params.name).then(function(resp){
      setProducts(resp.data)
    })

  }, [])

  useEffect(() => {
    if(params.name){
      reset(products);
    }
}, [products]);
  
  function onSubmit(data){
    // console.log(data[0].pic);
    // console.log(data);
    if(params.name){

      let meraForm = new FormData();
      meraForm.append("title",  data.title)
      meraForm.append("price",  data.price)
      // meraForm.append("id" , v4())

        for(let file of data.pic){
            meraForm.append("pic", file)
        }

      axios.put('/product-update', meraForm ).then(function(resp){
        console.log(resp.data);
        console.log(meraForm);
      })

      swal({
        title: "Success",
        text: "Product Updated Successfully",
        icon: "success",
        timer: 2000,
        button: false 
      })
      setTimeout(()=>{
        navigate("/adminDashboard");
      },2200)
    }
    else{

      // data.id = v4()
      
      data.userKiId = user.id
      
      let meraForm = new FormData();
      meraForm.append("title",  data.title)
      meraForm.append("price",  data.price)
      meraForm.append("userKiId",  data.userKiId)
      meraForm.append("id" , v4())

      for(let file of data.pic){
        meraForm.append("pic", file)
      }
      
      axios.post('/create-product', meraForm).then(function(){})

      swal({
        title: "Success",
        text: "Product Added Successfully",
        icon: "success",
        timer: 2000,
        button: false
      })
      setTimeout(()=>{
        navigate("/userDashboard");
      },2200)
  }
  }
  // console.log(watch('username'));
    
  return (
    <section className='App'>
        <div className="register">
            <div className="col-1">
                <h2>Products</h2>
                <span>Add your product here</span>

                <form id='form' className='flex flex-col' onSubmit={handleSubmit(onSubmit)}>
                    <input name='title' type="text" {...register("title" , { required : true })} placeholder='Title of product'  autoFocus  autoComplete='off' />
                    {errors.title?.type === "required" && "Title is required"}
                    <input name='price' type="text" {...register("price" , { required : true })} placeholder='Price of Product'  autoComplete='off' />
                    {errors.price?.type === "required" && "Price is required"}
                    <input name='pic' type="file" {...register("pic")}/>
                    <button className='btn'>Add Product</button>
                </form>

            </div>
            <div className="col-2">
                <img src={productimage} alt="File Not Accessed" />
            </div>
        </div>
    </section>
  )
}