import axios from 'axios'
import React, { useEffect, useState } from 'react'
import './Product.css'
import swal from 'sweetalert'
import { Link, useNavigate } from 'react-router-dom'

//              *****************************************************
export const Products = () => {

  let navigate = useNavigate();

  useEffect(()=>{
    axios.get('/product').then(
      function(res){
      setProduct(res.data) 
      // console.log(res.data);
    })
  },[])

  let [product, setProduct] = useState([])

  return (
    <div>
      <button onClick={()=>{
        navigate('/adminDashboard');
      }}>Go Back</button>
      {
        product.map(function(product,index){
          return(
            <>
              <div class="container">
                <div className='card-deck row '>
                  <div className='column'>                  
                    <div class="card">
                      {/* <!--Card image--> */}
                      <div class="view overlay">
                        <img class="card-img-top img_f" src={product.pic} alt="f"/>
                        <a href="#!">
                          <div class="mask rgba-white-slight"></div>
                        </a>
                      </div>
                      {/* <!--Card content--> */}
                      <div class="card-body">
                        {/* <!--Title--> */}
                        <h5 class="card-title">Title : {product.title}</h5>
                        <h5 class="card-title">Price : {product.price}</h5>
                        <h5 class="card-title">Pic Name : {product.pic}</h5>
                        {/* <!--Text--> */}
                        <button type="button" 
                                className="btn btn-light-blue btn-md btn_del"
                                onClick={()=>{
                                  axios.delete('/product-delete?anc=' + product.id).then(function(resp){
                                    setProduct(product.filter(myProduct=>myProduct.id !== product.id));
                                    console.log(resp.data);
                                  }); 
                                  console.log(product.id);
                                  swal({
                                        title: "Success",
                                        text: "Product Deleted Successfully",
                                        icon: "success",
                                        timer: 2000,
                                        button: false
                                      })
                                }}>Delete
                        </button>
                        <button type="button" 
                                class="btn btn-light-blue btn-md">
                                <Link to={'/productscreate/'+ product.id}>Update</Link>
                        </button>

                      </div>
                    </div>
                  </div>
                </div>
               </div>
            </>  
          );
        })
      }      
    </div>
  )
}
