import React, { useEffect, useState } from 'react'
import './UserDashboard.css'
import axios from 'axios'
import { Link } from 'react-router-dom'
// import { useSelector } from 'react-redux'


//                ****************************************************************
export const UserDashboard = () => {

  useEffect(()=>{
    axios.get('/product').then(
      function(res){
        setProducts(res.data) 
      // console.log(res.data);
    })
  },[])

  let [products, setProducts] = useState([])

  // products = useSelector(function(store){

  //   let userID = store.userSection.currentUser.id;

  //   products.filter(product=>product.userKiId === userID);
  // });

  // console.log(products);



  return (
    <div>
      <span id='user_d'>
      User Dashboard
      </span>
      <table>
            <thead>
              <tr>
                <td>Pic Name</td>
                <td>Title of Product</td>
                <td>Price of Product</td>
              </tr>
            </thead>
            {
                products.map(function(product){
                    return <tr>
                        <td><img id='image_f' src={product.pic} alt="f" /></td>
                        <td>{product.title}</td>
                        <td>{product.price}</td>
                    </tr>
                })
            }
        </table>
      <div>
      <button id='btnlink'><Link to='/productscreate'>Add Products</Link></button>
      </div>
    </div>
  )
}
