import React, { useState } from 'react'
import bgImg from '../../Images/img1.jpg';
import { useForm } from 'react-hook-form';
import './Login.css'
// import axios from 'axios';
// import { Link } from 'react-router-dom';
import { useNavigate } from "react-router-dom"
import swal from 'sweetalert';
import axios from 'axios';
import { useDispatch } from 'react-redux';


export function Login() {

  const navigate = useNavigate();
  
  let dispatch = useDispatch();

  let [passwordShow, setPasswordShow] = useState(false);

  const { register, handleSubmit, formState: { errors } } = useForm()

  async function onSubmit(data){

    let abc = await axios.post('/login',  data)
    let resp = abc.data;
    // console.log(res);

    // data.id = v4();
    //   axios.post('/create-user', data).then(function(){
    //     // toast.success("User Created")
    //   });

      if(resp){
        console.log(resp);
        localStorage.setItem('userToken', resp.userToken);

        dispatch({
          type:"LOGIN_HOGYA",
          payload:resp.userMilgya
        });
        
        if (resp.userMilgya.username === 'talha') {
          setTimeout(()=>{
            navigate("/adminDashboard");
          },2200)  
          swal({
            title: "Success",
            text: "Admin Login Successfully",
            icon: "success",
            timer: 2000,
            button: false
          })
        }
        else{
          setTimeout(()=>{
            navigate("/userDashboard");
          },2200)
          swal({
            title: "Success",
            text: "User Login Successfully",
            icon: "success",
            timer: 2000,
            button: false
          })
        }
      }
      else{
        swal({
          title: "Error",
          text: "User not found",
          icon: "error",
          timer: 2000,
          button: false
        })
      }
  }

    // console.log(watch('username'));
    
  return (
    <section className='App'>
        <div className="register">
            <div className="col-1">
                <h2>Sign In</h2>
                <span>Login and enjoy the service</span>

                  <form id='form' className='flex flex-col' onSubmit={handleSubmit(onSubmit)}>
                    <input type="text" {...register("username" , { required : true })} placeholder='Username' autoComplete='off' autoFocus/>
                    {errors.username?.type === "required" && "Username is required"}
                    <input 
                    type={ passwordShow === true ? "text" : "password"} 
                    {...register("password", { required : true})} 
                    placeholder='Password'
                    autoComplete='off' />
                    <img onClick={function() {
                      setPasswordShow(!passwordShow)
                    }}
                    className='pass_icon'
                    src={passwordShow === false ? "../../Images/hide-password-icon.jpg" : '../../Images/show-password-icon.jpg'} 
                    alt="File not present" />
                    {errors.password?.type === "required" && "Password is required"}
                    <button className='btn'>Sign In</button>
                </form>
            
            </div>
            <div className="col-2">
                <img src={bgImg} alt="File Not Accessed" />
            </div>
        </div>
    </section>
  )
}