import React, { useEffect, useState } from 'react'
import bgImg from '../../Images/img1.jpg';
import { useForm } from 'react-hook-form';
import './Register.css'
import {v4} from 'uuid';
import axios from 'axios';

// import { Link } from 'react-router-dom';
import { useNavigate, useParams } from "react-router-dom"
import swal from 'sweetalert';
import { toast } from 'react-toastify';


export function Register() {
  const navigate = useNavigate();
  let params = useParams();
  const [user, setUser] = useState(null);
  // console.log(user);

  useEffect(function(){
    // console.log(params);
    axios.get('/user-lao?id=' + params.name).then(function(resp){
      setUser(resp.data)
      // console.log(resp.data);
      
    })

  }, [])

  useEffect(() => {
    if(params.name){
      reset(user);
    }
}, [user]);



  let [passwordShow, setPasswordShow] = useState(false);
  const { register, handleSubmit, formState: { errors }, reset } = useForm()
  
  function onSubmit(data){

    if(params.name){
      
      // data.id = v4()
      data.id = v4()
      let meraForm = new FormData();
      meraForm.append("username",  data.username)
      meraForm.append("email",  data.email)
      meraForm.append("password",  data.password)
      meraForm.append("mobile",  data.mobile)
      meraForm.append("id" , v4())
          
        for(let file of data.pic){
            meraForm.append("pic", file)
        }

      axios.put('/user-update',meraForm ).then(function(resp){
        console.log(resp.data);
      }).catch(function(resp){
        console.log(resp);
      })

      // .catch((err) => {
      //   console.log(err);
      //   if(err.status === 403){
      //     swal({
      //       title: "Success",
      //       text: "User Updated Successfully",
      //       icon: "success",
      //       timer: 2000,
      //       button: false
      //     })
      //     //  alert("email already used");
      //   }
      // });

      // dispatch({
      //   // meraIndex:userKaIndex,
      //   type:"PERSON_EDIT",
      //   payload:data
      // })

      swal({
        title: "Success",
        text: "User Updated Successfully",
        icon: "success",
        timer: 2000,
        button: false
      })
      setTimeout(()=>{
        navigate("/users");
      },2200)

    }else{

      data.id = v4()
      let meraForm = new FormData();
      meraForm.append("username",  data.username)
      meraForm.append("email",  data.email)
      meraForm.append("password",  data.password)
      meraForm.append("mobile",  data.mobile)
      meraForm.append("id" , v4())
          
        for(let file of data.pic){
            meraForm.append("pic", file)
        }
        
      axios.post('/create-user', meraForm).then(function(){
      }).catch(function(resp){
        console.log(resp.response.data.message);
      })
      // }).catch(function(resp){
      //   swal({
      //     title: "Error",
      //     text: resp.response.data.message,
      //     icon: "error",
      //     timer: 2000,
      //     button: false
      //   })
      //   console.log(resp.response.data.message);
      // });
      // console.log();
      // console.log(data.id);
    // data.id = v4();
    //   axios.post('/create-user', data).then(function(){
    //     // toast.success("User Created")
    //   });

      swal({
        title: "Success",
        text: "User Added Successfully",
        icon: "success",
        timer: 2000,
        button: false
      })
      setTimeout(()=>{
        navigate("/login");
      },2200)
  }
}
    // console.log(watch('username'));
    
  return (
    <section className='App'>
        <div className="register">
            <div className="col-1">
                <h2>Sign Up</h2>
                <span>Register yourself and enjoy the service</span>

                <form id='form' className='flex flex-col' onSubmit={handleSubmit(onSubmit)}>
                    <input name='username' type="text" {...register("username" , { required : true })} placeholder='Username'  autoFocus  autoComplete='off' />
                    {errors.username?.type === "required" && "Username is required"}
                    <input name='email' type="text" {...register("email" , { required : true })} placeholder='Email'  autoComplete='off' />
                    {errors.email?.type === "required" && "Email is required"}
                    <input name='password'  
                    type={ passwordShow === true ? "text" : "password"} 
                    {...register("password", { required : true})} 
                    placeholder='Password'
                    autoComplete='off' />
                    <img onClick={function() {
                      setPasswordShow(!passwordShow)
                    }}
                    className='p_icon'
                    src={passwordShow === false ? "../../Images/hide-password-icon.jpg" : '../../Images/show-password-icon.jpg'} 
                    alt="File not present" />
                    {errors.password?.type === "required" && "Password is required"}
                    <input name='mobile' type="text" {...register("mobile", { required : true})} placeholder='Mobile Number'  autoComplete='off' />
                    {errors.mobile?.type === "required" && "Mobile Number is required"}
                    <input name='pic' id='pic' type="file" {...register("pic", { required : true})}/>
                    <button className='btn'>Sign Up</button>
                </form>

            </div>
            <div className="col-2">
                <img src={bgImg} alt="File Not Accessed" />
            </div>
        </div>
    </section>
  )
}