import axios from 'axios'
import React, { useEffect, useState } from 'react'
import './Card.css'
import swal from 'sweetalert'
import { Link, useNavigate } from 'react-router-dom'

//              *****************************************************
export const Card = () => {
let navigate = useNavigate();
  useEffect(()=>{
    axios.get('/user').then(
      function(res){
      setUsers(res.data) 
      // console.log(res.data);
    })
  },[])

  let [users, setUsers] = useState([])

  return (
    <div>
      <button onClick={()=>{
        navigate('/adminDashboard');
      }}>Go Back</button>
      {
        users.map(function(user,index){
          return(
            <>
              <div class="container">
                <div className='card-deck row '>
                  <div className='column'>                  
                    <div class="card">
                      {/* <!--Card image--> */}
                      <div class="view overlay">
                        <img class="card-img-top img_f" src={user.pic} alt="f"/>
                        <a href="#!">
                          <div class="mask rgba-white-slight"></div>
                        </a>
                      </div>
                      {/* <!--Card content--> */}
                      <div class="card-body">
                        {/* <!--Title--> */}
                        <h5 class="card-title">Username : {user.username}</h5>
                        <h5 class="card-title">Email : {user.email}</h5>
                        <h5 class="card-title">Password : {user.password}</h5>
                        <h5 class="card-title">Mobile No : {user.mobile}</h5>
                        {/* <!--Text--> */}
                        <button type="button" 
                                className="btn btn-light-blue btn-md btn_del"
                                onClick={()=>{
                                  axios.delete('/user-delete?anc='+user.id).then(function(resp){
                                    setUsers(users.filter(myUser=>myUser.id !== user.id));
                                    // console.log(resp.data);
                                  }); 
                                  // console.log(user.id);
                                  swal({
                                        title: "Success",
                                        text: "User Deleted Successfully",
                                        icon: "success",
                                        timer: 2000,
                                        button: false
                                      })
                                }}>Delete
                        </button>
                        <button type="button" 
                                class="btn btn-light-blue btn-md">
                                <Link to={'/register/'+ user.id}>Update</Link>
                        </button>

                      </div>
                    </div>
                  </div>
                </div>
               </div>
            </>  
          );
        })
      }      
    </div>
  )
}
